* {
    height: 100%;
    padding: 0;
    font-family: 'Sulphur Point', sans-serif;
}

body {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    padding: 0;
    text-align: center;
    background: white;
}

header {
    margin: auto;
    padding: 3em;

}

#headerTitle {
    font-family: 'Roboto Mono', monospace;
    font-size: 4em;
    text-align: left;
    vertical-align: middle;
    margin: 0;
    color: rgb(117, 233, 233)
}

#dashboardContainer,
main {
    width: 100vw;
    height: auto;
    display: block;
}

#contenContainer {
    width: 100%;
    height: auto;
    margin: auto;
    vertical-align: middle;
    text-align: center;
    display: inline-grid;
    grid-template-columns: 3fr 1fr;
    grid-row-gap: 0;
    grid-column-gap: 0;
}

div {
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    display: block;
}


h3 {
    height: auto;
}

#Header {
    width: auto;
    height: 200px;
    margin: 0;
    top: 0;
    color: white;
    padding: 2%;
}

#navLink{
    color: white;
    padding: 2%;
}

#Header::after {
    width: 100vw;
    content: "";
    background-image: url("https://raw.githubusercontent.com/tsimpkins73/theConsole/master/public/headerImage.jpeg");
    background-size: cover;
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}

h1 {
    height: auto;
}

navbar {
    width: 100%;
    grid-template-columns: 2fr 2fr 2fr;
}

#navbar {
    width: 100%;
    height: 30px;
    position: -webkit-sticky;
    position: sticky;
    background: none;
    color: white;
   display: inline-block;
   padding-top: 1%;
   padding-bottom: 1%;
   font-size: 2em;
}
